@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@1,100&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #FBF9F4;
  background: linear-gradient(rgba(255, 57, 57,0.3),10%,#FBF9F4);
  background-attachment:fixed;
  margin: 0 auto;
  width: 90%;
}

.header{
  display: flex;
  align-items: center;
  justify-content:space-between;
  margin-top: 10px;
}

.headerImgLogo1{
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
}
.headerImgLogo1Line{
  position: absolute;
  width: 250px;
}

.headerImgLogo2{
  display: flex;
  align-items: flex-end;
  gap: 30px;
}

.searchHeader{
  display: flex;
  align-items: center;
  justify-content:space-between;
  margin-top: 10px;
}

.header img{
}

.headerMenu ul{
  display: flex;
  flex-wrap: wrap;
}

.headerMenu li{
  width: 33%;
  margin-bottom: 10px;
  box-sizing: border-box;
  list-style-type: none;
  font-size: 12px;
  font-weight: bold;
}

.search {
  margin-left: -100px;
  margin-top: 30px;
  min-height: 100%;
}

.search input{
  border: 1.5px solid #292929;
  width: 350px;
  padding: 10px;
  padding-left: 30px;
  margin-right: 10px;
}

.search input::placeholder{
  
}

.search input:focus{
  outline: none;
}

.search button{
  background-color: #FF3939;
  border: none;
  color: #FBF9F4;
  padding: 5px;
  width: 100px;
}

.mainContent{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 100px;
  margin-top: 50px;
}

.subContents{
  display: flex;
  flex-wrap: wrap;
  gap: 5%;
  margin-bottom: 50px;
}

.subContent{
  display: flex;
  margin-top: 50px;
  gap: 20px;
  width: 45%;
  height: 205px;
  align-items: flex-start;
}

.mainContentTitle, .subContentTitle {
  font-weight: bold;
}

.mainContentText, .subContentText, .articleSubContent {
  font-family: 'Noto Sans', sans-serif;
}

.mainContentText{
  font-size: 24px;
}

.mainContentAuthor, .subContentAuthor {
  color: #969696;
  margin-top: 10%;
  font-size: 11px;
}

.mainContentDate, .subContentDate {
  color: #969696;
  font-size: 11px;
}

.searchPageHeading{
  text-align: center;
  margin-top: 100px;
  color: #4A4A4A;
  line-height: 30px;
  font-size: 14px;
}

.searchPageForm{
  margin-top: 50px;
  text-align: center;
  background-color: white;
  width: 500px;
  height: 50px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.35);
}

.searchPageForm input{
  background: none;
  border: none;
  padding: 10px;
  width: 75%;
}

.searchPageForm input:focus{
  outline: none;
}

.searchPageForm button{
  background: #FF3939;
  border: none;
  padding: 7px;
  width: 25%;
  margin: 5px 10px 5px 5px;
  color: white;
}

.articlePageHeding{
  color: #969696;
  margin: 100px auto;
  text-align: center;
}

.article{
  margin: 0 auto;
  width: 50%;
}

.articleHeader{
  border-bottom: 0.6px solid;
}

.articleHeader p{
  color: #535353;
}

.articleHeaderDate{
  font-size: 12px;
  font-style: italic;
  margin-top: 30px;
  margin-bottom: 30px;
}

.snsIcon{
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 40px;
}

.snsIcon1{
  background-color: #36BEDC;
  font-size: 12px;
  color: #FCFEFC;
  padding: 2px;
  width: 220px;
}

.snsIcon2{
  background-color: #1459A9;
  font-size: 12px;
  color: #FCFEFC;
  padding: 2px;
  width: 220px;
}

.snsIcon1 p, .snsIcon2 p{
  color: #FCFEFC;
}

.articleHeadline,.articleFooter {
  margin-top: 50px;
  margin-bottom: 50px;
  line-height: 35px;
}

.articleFooter{
  border-bottom: 0.6px solid;
}

.articleFooter p {
  margin-bottom: 50px;
}

.articleContent p{
  margin-bottom: 30px;
}

.articleVideo{
  width:100%;
  height: 300px;
  background: linear-gradient(black,gray);
  position: relative;
}
.articleVideoTitle{
  color:#ffffff;
  font-family: 'Noto Sans', sans-serif;
  position: absolute;
  top: 3%;
  left: 10%;
}

.articleVideoPlayButton{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.articleVideoPlayButton button {
  background-color: #FF3939;
  border: none;
  color: #FBF9F4;
  text-align: center;
  width: 70px;
  height: 70px;
  font-size: 30px;
}

.articleSubContent{
  font-size: 13px;
  line-height: 25px;
  margin: 100px 0;
}

.loading{
  width: 300px;
  margin: 100px auto;
  text-align: center;
  color: #979797;
}

.loading div{
  display: flex;
  justify-content: center;
}
